<template>
	<div>
		<v-usersearch v-on:listenAdd="Add">
		</v-usersearch>
		<v-adduser ref="add" v-on:getUserList="getUserList"></v-adduser>
		<template>
			<el-table :header-cell-style="{background:'#DFEBF9',color:'#606266'}" :data="user_list" :empty-text="$t('common.nodata')"
			 border>
				<el-table-column :label="$t('common.No')" type="index" width="50">
				</el-table-column>
				<el-table-column prop="user_id" :label="$t('user.id')">
				</el-table-column>
				<el-table-column prop="user_name" :label="$t('user.user_name')">
				</el-table-column>
				<el-table-column prop="password" :label="$t('user.pass_word')">
				</el-table-column>
				<el-table-column prop="user_group_power" :label="$t('user.power')">
				</el-table-column>
				<el-table-column prop="description" :label="$t('user.des')">
				</el-table-column>
				<el-table-column prop="" :label="$t('common.operate')" width="158">
					<template slot-scope="scope">
						<el-button type='primary' size="mini" @click="editUser(scope.row)" :disabled="permissionFlag">{{$t('common.edit')}}</el-button>
						<el-button type='danger' size="mini" @click="deleteUser(scope.row.user_id)" :disabled="permissionFlag?true:false">{{$t('common.delete')}}</el-button>
					</template>
				</el-table-column>
			</el-table>
		</template>
	</div>
</template>
<script>
	import VUsersearch from './Search'
	import VAdduser from './Add'
	import {
		Debounce,
		permission
	} from '@/utils/public'
	import {
		userList,
		deleteUser
	} from '@/request/api/user'; // 导入api接口
	export default {
		components: {
			VUsersearch,
			VAdduser
		},
		data() {
			return {
				permissionFlag: permission(),
				user_list: [],
			}
		},
		created() {

		},
		mounted() {
			this.getUserList();
		},
		methods: {
			// 获取用户列表
			getUserList() {
				userList({}).then(res => {
					if (res.result) {
						this.user_list = res.data
					} else {
						this.$message({
							showClose: true,
							message: res.msg,
							type: 'error'
						})
					}
				})
			},
			Add() {
				this.$refs.add.open()
			},
			editUser:Debounce(function(row){
				this.$refs.add.open(row)
			}),
			deleteUser(id) {
				this.$confirm(this.$t('user.deltips'), this.$t('user.tips'), {
					confirmButtonText: this.$t('common.sure'),
					cancelButtonText: this.$t('common.cancel'),
					type: 'warning'
				}).then(() => {
					deleteUser({
						user_id: id
					}).then(res => {
						if (res.result) {
							this.getUserList()
							this.$message(this.$t('user.delsuccess'))
						}else{
							this.$message(res.msg)
						}
					})
				}).catch(() => {
					// this.$message({
					//   type: 'info',
					//   message: '已取消删除'
					// });          
				});
			},

		}
	}
</script>
