<template>
	<div>
		<el-dialog :title="$t('user.adduser')" :close-on-click-modal="false" :visible.sync="addVisible" @close="reset('userform')"
		 width="760px">
			<div class="add_user_form">
				<el-form ref="userform" :inline="true" :model="userform" :rules="rules" label-width="100px">
					<el-form-item :label="$t('user.user_name')" prop="user_name">
						<el-input v-model="userform.user_name"></el-input>
					</el-form-item>
					<el-form-item :label="$t('user.power')" prop="user_group_power">
						<el-select v-model="userform.user_group_power" value-key="id">
							<el-option v-for="item in grouplist" :key="item.id" :label="item.group_name" :value="item.id">
							</el-option>
						</el-select>
					</el-form-item>
					<el-form-item :label="$t('user.password')" prop="password" >
						<el-input v-model="userform.password" show-password auto-complete="new-password"></el-input>
					</el-form-item>
					<el-form-item :label="$t('user.des')" prop="description">
						<el-input v-model="userform.description" ></el-input>
					</el-form-item>
					<div class="operate_form_btn">
						<el-button @click="userCancel('userform')">{{$t('common.cancel')}}</el-button>
						<el-button type="primary" @click="userSave('userform')">{{$t('common.save')}}</el-button>
					</div>

				</el-form>
			</div>
		</el-dialog>
	</div>
</template>
<script>
	import {
		Debounce
	} from '@/utils/public'
	import {
		addUser
	} from '@/request/api/user'; // 导入api接口
	export default {
		data() {
			return {
				addVisible: false,
				grouplist: [{'group_name':'管里员',id:0},{'group_name':'操作员',id:1}],
				userform: {
					user_id:"",
					password: "",
					user_name: "",
					user_group_power: "",
					description:""
				},
				rules: {
					user_name: [
						{
							required: true,
							message: this.$t('user.userholder'),
							trigger: 'blur'
						},
						{
							validator: (rule, value, callback) => {
								if (/[\u4e00-\u9fa5_a-zA-Z0-9_]{2,16}/.test(value) == false) {
									callback(new Error(this.$t('user.usertips')));
								} else {
									callback();
								}
							},
							trigger: 'blur'
						},
					],
					password: [{
						required: true,
						message: this.$t('user.passwordholder'),
						trigger: 'blur'
					}],
					user_group_power: [{
						required: true,
						message: this.$t('user.authorizationholder'),
						trigger: 'change'
					}],
				}
			}
		},
		mounted() {
			this.userform.add_user_passwd = ''
			this.userform.add_user_name = ''
		},
		methods: {

			open(row) {
				if(row){
					this.userform.user_id= row.user_id
					this.userform.user_group_power= row.user_group_power;
					this.userform.password = row.password;
					this.userform.user_name = row.user_name;
					this.userform.description = row.description;
				}else{
					this.userform.user_id= ""
					this.userform.user_group_power= "";
					this.userform.password = "";
					this.userform.user_name = "";
					this.userform.description = "";
				}
				this.addVisible = true;
			},
			reset(userform) {
				this.$refs.userform.resetFields();
			},
			// 保存用户
			userSave: Debounce(function(userform) {
				this.$refs.userform.validate((valid)=>{
					if(valid){
						addUser(this.userform).then(res =>{
							if (res.result) {
								this.$message.success(this.$t('user.infosuccess'))
								this.addVisible = false
								this.$emit('getUserList')
							} else{
								this.$message.error(res.msg)
							}
						})
					}else{
						return false
					}
				})									
			}),
			// 取消保存
			userCancel(userform) {
				this.$refs.userform.resetFields();
				this.addVisible = false;
			}
		}
	}
</script>
<style >
.add_user_form .el-input__inner{
	width:220px;
}
.operate_form_btn .el-button{
	width:100px;
	margin:0 20px;
}
.add_user_form .el-scrollbar{
	padding-bottom: 16px!important;
}
</style>
